import React from "react";
import WelcomSlides from "../Element/Home/WelcomeSlides";
import TopCategoryArea from "../Element/Home/TopCategoryArea";
import NewArrivalsArea from "../Element/Home/NewArrivalsArea";
import OfferArea from "../Element/Home/OfferArea";
import Testimonials from "../Element/Home/Testimonials";

function Home() {
  return (
    <section>
      <div id="wrapper">
        {/* ****** Welcome Slides Area Start ****** */}
        <WelcomSlides />
        {/* ****** Welcome Slides Area End ****** */}

        {/* ****** Top Category Area Start ****** */}
        <TopCategoryArea />
        {/* ****** Top Category Area End ****** */}

        {/* ****** Quick View Modal Area Start ****** */}
        <div
          class="modal fade"
          id="quickview"
          tabindex="-1"
          role="dialog"
          aria-labelledby="quickview"
          aria-hidden="true"
        >
          <div
            class="modal-dialog modal-lg modal-dialog-centered"
            role="document"
          >
            <div class="modal-content">
              <button
                type="button"
                class="close btn"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span aria-hidden="true">&times;</span>
              </button>

              <div class="modal-body">
                <div class="quickview_body">
                  <div class="container">
                    <div class="row">
                      <div class="col-12 col-lg-5">
                        <div class="quickview_pro_img">
                          <img
                            src="assets/img/product-img/product-1.jpg"
                            alt=""
                          />
                        </div>
                      </div>
                      <div class="col-12 col-lg-7">
                        <div class="quickview_pro_des">
                          <h4 class="title">Boutique Silk Dress</h4>
                          <div class="top_seller_product_rating mb-15">
                            <i class="fa fa-star" aria-hidden="true"></i>
                            <i class="fa fa-star" aria-hidden="true"></i>
                            <i class="fa fa-star" aria-hidden="true"></i>
                            <i class="fa fa-star" aria-hidden="true"></i>
                            <i class="fa fa-star" aria-hidden="true"></i>
                          </div>
                          <h5 class="price">
                            $120.99 <span>$130</span>
                          </h5>
                          <p>
                            Lorem ipsum dolor sit amet, consectetur adipisicing
                            elit. Mollitia expedita quibusdam aspernatur,
                            sapiente consectetur accusantium perspiciatis
                            praesentium eligendi, in fugiat?
                          </p>
                          <a href="#">View Full Product Details</a>
                        </div>
                        {/* Add to Cart Form */}
                        <form class="cart" method="post">
                          <div class="quantity">
                            <span
                              class="qty-minus"
                              onclick="var effect = document.getElementById('qty'); var qty = effect.value; if( !isNaN( qty ) &amp;&amp; qty &gt; 1 ) effect.value--;return false;"
                            >
                              <i class="fa fa-minus" aria-hidden="true"></i>
                            </span>

                            <input
                              type="number"
                              class="qty-text"
                              id="qty"
                              step="1"
                              min="1"
                              max="12"
                              name="quantity"
                              value="1"
                            />

                            <span
                              class="qty-plus"
                              onclick="var effect = document.getElementById('qty'); var qty = effect.value; if( !isNaN( qty )) effect.value++;return false;"
                            >
                              <i class="fa fa-plus" aria-hidden="true"></i>
                            </span>
                          </div>
                          <button
                            type="submit"
                            name="addtocart"
                            value="5"
                            class="cart-submit"
                          >
                            Add to cart
                          </button>
                          {/* Wishlist */}
                          <div class="modal_pro_wishlist">
                            <a href="wishlist.html" target="_blank">
                              <i class="ti-heart"></i>
                            </a>
                          </div>
                          {/* Compare */}
                          <div class="modal_pro_compare">
                            <a href="compare.html" target="_blank">
                              <i class="ti-stats-up"></i>
                            </a>
                          </div>
                        </form>

                        <div class="share_wf mt-30">
                          <p>Share With Friend</p>
                          <div class="_icon">
                            <a href="#">
                              <i class="fa fa-facebook" aria-hidden="true"></i>
                            </a>
                            <a href="#">
                              <i class="fa fa-twitter" aria-hidden="true"></i>
                            </a>
                            <a href="#">
                              <i class="fa fa-pinterest" aria-hidden="true"></i>
                            </a>
                            <a href="#">
                              <i
                                class="fa fa-google-plus"
                                aria-hidden="true"
                              ></i>
                            </a>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        {/* ****** Quick View Modal Area End ****** */}

        {/* ****** New Arrivals Area Start ****** --> */}
        <NewArrivalsArea />
        {/* ****** New Arrivals Area End ****** --> */}

        {/* -- ****** Offer Area Start ****** --> */}
        <OfferArea />
        {/* -- ****** Offer Area End ****** --> */}

        {/* <!-- ****** Popular Brands Area Start ****** --> */}
        <Testimonials />
        {/* <!-- ****** Popular Brands Area End ****** --> */}
      </div>
    </section>
  );
}

export default Home;
